import React, { Component } from 'react';

class Testimonials extends Component {
  render() {

    if(this.props.data){
      var testimonials = this.props.data.testimonials.map(function(testimonials){
        return  <li key={testimonials.user}>
            <blockquote>
               <p>{testimonials.text}</p>
               <p>{testimonials.text1}</p>
               <p>{testimonials.text2}</p>
               <p>{testimonials.text3}</p>
               <p>{testimonials.text4}</p>
               <p>{testimonials.text5}</p>
            </blockquote>
         </li>
      })
    }

    return (
      <section id="testimonials">
      <div className="text-container">
         <div className="row">

            <div className="twelve columns header-col">
               <h1 align="center"><span>Achievements</span></h1>
            
            <ul>
            <li> {'>>'}  <bold>  CODECHEF : </bold>   Highest Rating - 3* (1771 pts)</li>
            <li> {'>>'}  Ranked <bold>306th / 30K participants</bold> in  Codechef May Long Challenge 2021</li>
            <li> {'>>'}  <bold>84 percentile </bold>-  April 2021 Elevate by Codechef-Scholarship Aptitude Test </li>
            <li> {'>>'}  <bold>  CODEFORCES : </bold>   Highest Rating - Newbie (1127 pts)</li>
            <li> {'>>'}  <bold>  HACKERRANK : </bold>   Highest Rating - 5* @ (Problem Solving)</li>
            <li> {'>>'}  Skill Certification in <bold>Problem Solving , Python, SQL</bold> by HACKERRANK</li>
            <li> {'>>'}  <bold>TechGig Code Gladiators </bold> 2021: 1063rd / 60K participants Coding Contest.</li>
            </ul>
            </div>
            
            </div>
         </div>
   </section>
    );
  }
}

export default Testimonials;
