import React, { Component } from 'react';

const encode = (data) => {
    return Object.keys(data)
      .map(key => encodeURIComponent(key) + "=" + encodeURIComponent(data[key]))
      .join("&");
}

class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      message: '',
    }
  }

  handleSubmit = e => {
    fetch("/", {
      method: "POST",
      headers: { "Content-Type": "application/x-www-form-urlencoded" },
      body: encode({ "form-name": "contact-me", ...this.state })
    })
      .then(() => {
        alert("Your message was successfully sent!");
        this.setState({
          name: "",
          email: "",
          message: ""
        })
      })
      .catch(error => alert(error));
    e.preventDefault();
  };

  handleChange = e => this.setState({
    [e.target.name]: e.target.value,
  });

  render() {

    if(this.props.data){
      var message = this.props.data.contactmessage;
    }

    return (
      <section id="contact">
        <div className="row section-head">
          <div className="twelve columns">
            <h1>COMMUNITY INVOLVEMENTS :</h1>
            <a href="https://www.codechef.com/users/u_c0de"><img src="images/thumbs/1.png" width="80" height="80" alt="Codechef" /></a>
            <a href="https://codeforces.com/profile/CR0SS"><img src="images/thumbs/2.jpg" width="80" height="80" alt="Codeforce" /></a>
            <a href="https://www.hackerrank.com/infoutkarsh"><img src="images/thumbs/6.png" width="80" height="150" alt="HR" /></a>
            <a href="https://gssoc.girlscript.tech/"><img src="images/thumbs/3.png" width="80" height="100" alt="GirlScript" /></a>
            <a href="https://auth.geeksforgeeks.org/user/infoutkarsh/articles"><img src="images/thumbs/4.png" width="80" height="100" alt="GFG" /></a>
            <a href="https://developers.google.com/community/dsc"><img src="images/thumbs/5.png" width="85" height="120" alt="GDC" /></a>
            <p className="lead">{message}</p>
          </div>
        </div>
        </section>
    );
  }
}

export default Contact;
